@import '../../variables.scss';

.simpleInput input{

    width: -webkit-fill-available;
    height: fit-content;

    padding: 6px 12px ;

    font-size: 14px;

    background: $background;

    border-radius: 8px;
    border:2px solid $main-light;

    outline: none;
    opacity: 0.8;

    &:hover{
        opacity: 1;
    }
    &:focus{
        border-color: $main;
    }


    ~label{
        color: $error;
        font-size: 12px;
        font-weight: 500;
        display: block;
        text-align: end;
        opacity: 0;
        margin-right: 8px;
    }
    &.error{
        border-color:$error;
        ~label{
            opacity: 1;
        }
    }

}
.checkBoxInput input{
        cursor: pointer;
        opacity: 0;
        position: absolute;
        ~label{
            cursor: pointer;
            overflow-wrap: break-word;
            position: relative;
            width: 100%;
            
            
            &::before{
                content:'';

                margin-right: 8px;
                display:inline-block;

                height: 12px;
                width: 12px;

                border:2px solid $main ;
                border-radius: 3px;
                background: $background;
            }
        }
        &:checked{
            ~label{
                font-weight: 500;
                opacity: 1;

                
                &:after{
                    content:'';
                    position: absolute;
                    left: 3px;
                    top: 4px;
                    height: 10px;
                    width: 10px;

                    border-radius: 2px;
                    background: $main;
                }
            }
        }
}
.radioButtonInput input{
    cursor: pointer;
    opacity: 0;
    position: absolute;
    ~label{
        cursor: pointer;
        overflow-wrap: break-word;
        position: relative;
        width: 100%;
        
        
        &::before{
            content:'';

            margin-right: 8px;
            display:inline-block;

            height: 12px;
            width: 12px;

            border:2px solid $main ;
            border-radius: 50%;
            background: $background;
        }
    }
    &:checked{
        ~label{
            font-weight: 500;
            opacity: 1;

            
            &:after{
                content:'';
                position: absolute;
                left: 3px;
                top: 4px;
                height: 10px;
                width: 10px;

                border-radius: 50%;
                background: $main;
            }
        }
    }
}